import {LoadingController} from 'ionic-angular';
import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { DirectoryBrowserViewModel } from "../../Model/ViewModel/DirectoryBrowserViewModel";

/**
 * Generated class for the DirectoryBrowserModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-directory-browser-add-edit-modal",
  templateUrl: "directory-browser-add-edit-modal.html"
})
export class DirectoryBrowserAddEditModalPage {
  item: any;
  mode: string;
  CanEditFileBrowser: boolean;
  title:string;
   constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController,
  ) {

    this.item = new DirectoryBrowserViewModel();
    this.item.filepath = navParams.data.item.filepath;
    this.item.directory_name = navParams.data.item.directory_name;
    this.item.create_time = navParams.data.item.create_time;


    this.CanEditFileBrowser = navParams.data.CanEditFileBrowser;
    this.mode = navParams.data.mode;
    if(this.mode==="POST")
      this.title="新增資料夾";
    else
    this.title="變更資料夾";
  }

  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad DirectoryBrowserModalPage");
  }
}



